import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  isMobile: Boolean = false;

  getIsMobile() {
    if (window.screen.width <= 360) {
      return this.isMobile === true;
    }
  }
}
